import { Component, OnInit } from '@angular/core';
import { PerfilUsuario } from '../../../model/objetc/perfil-usuario';
import { PermissoesAcessoService } from '../../../service/sigobra/permissoes-acesso.service';
import { Base } from '../../../model/base';
import { Router } from '@angular/router';

@Component({
  selector: 'app-perfil-usuario',
  templateUrl: './perfil-usuario.page.html',
  styleUrls: ['./perfil-usuario.page.scss'],
})
export class PerfilUsuarioPage implements OnInit {

  perfisUsuarios: Array<PerfilUsuario>;
  value: string = ' ';
  constructor(private pa: PermissoesAcessoService,
    private base: Base, private router: Router) {

  }

  ngOnInit() {

  }

  ionViewWillEnter() {
    this.pesquisarPerfilUsuario1();
  }

  inserir() {
    this.pa.perfilUsuario = null;
    this.router.navigate(['perfil-usuario-inserir']);
  }

  permissaoUsuario() {
    this.router.navigate(['permissao-usuario']);
  }

  editar(perfilUsuario: PerfilUsuario) {
    this.pa.perfilUsuario = perfilUsuario;
    this.router.navigate(['perfil-usuario-inserir']);
  }

  pesquisarPerfilUsuario(value: string) {
    this.base.present();
    this.pa.pesquisarPerfilUsuario(value).subscribe(
      data => {
        this.perfisUsuarios = data as Array<PerfilUsuario>;
        this.base.dismiss();
      },
      error => {
        this.base.mensagemErro('Falha ao consultar perfil usu치rio ' + this.base.tratarErro(error));
        this.base.dismiss();
      }
    );
  }

  deletar(perfilUsuario: PerfilUsuario) {
    this.base.present();
    this.pa.deletarPerfilUsuario(perfilUsuario).subscribe(
      data => {
        this.perfisUsuarios = data as Array<PerfilUsuario>;
        this.base.dismiss();
      },
      error => {
        this.base.mensagemErro('Falha ao excluir perfil usu치rio ' + this.base.tratarErro(error));
        this.base.dismiss();
      }
    );
  }

  pesquisarPerfilUsuario1() {
    this.base.present();
    this.pa.pesquisarPerfilUsuario1(this.value).subscribe(
      data => {
        this.perfisUsuarios = data as Array<PerfilUsuario>;
        this.base.dismiss();
      },
      error => {
        this.base.mensagemErro('Falha ao consultar perfil usu치rio ' + this.base.tratarErro(error));
        this.base.dismiss();
      }
    );
  }

}
